//ÂºïÂÖ•axios
import axios from 'axios'
//ÂºïÂÖ•vuex
import store from './store'
//ÂºïÂÖ•Ë∑ØÁî±
import router from './router'


//httpÂÖ®Â±ÄÊã¶Êà™
//tokenË¶ÅÊîæÂú®ËØ∑Ê±ÇÁöÑheader‰∏≠Â∏¶ÁªôÂêéÁ´ØÊé•Âè£
export default function sexAxios(){
  // Ê∑ªÂä†ËØ∑Ê±ÇÊã¶Êà™Âô®ÔºàËØ•ÂÜôÊ≥ïÊòØÂõ∫ÂÆöÁöÑÔºâÔºåÁÑ∂ÂêéÂú®ÈáåÈù¢ÂÜô‰∏Ä‰∏™configÈÖçÁΩÆ
  axios.interceptors.request.use(config => {
    //Â¶ÇÊûústate‰∏≠ÊâÄÂ≠òÂÇ®ÁöÑtokenÁöÑÂÄº‰∏çÊòØÁ©∫Â≠óÁ¨¶‰∏≤(Ê≥®:ÈùûÁ©∫Â≠óÁ¨¶‰∏≤ÁöÑÂ∏ÉÂ∞îÂÄº‰∏∫true)ÔºåÂ∞±Â∞ÜËØ•tokenÊ∑ªÂä†Âà∞configÈÖçÁΩÆ‰∏≠ÔºåÁÑ∂ÂêéreturnËØ•ÈÖçÁΩÆ
    if(store.state.token){
      config.headers.token = store.state.token
    }
    return config
  })

  //Ê∑ªÂä†ÂìçÂ∫îÊã¶Êà™Âô®
  axios.interceptors.response.use(response => {
    //Â¶ÇÊûúËØ∑Ê±ÇÁä∂ÊÄÅÁ†ÅÁ≠â‰∫é200
    if(response.status == 200){
      const data = response.data //ÈÄöËøáËµãÂÄºÁÆÄÂåñresponse.data‰∏∫dataÔºåÊñπ‰æø‰π¶ÂÜô
      //code=-1Ë°®Á§∫ÁôªÂΩïËøáÊúü
      if(data.code == -1){
        //ÁôªÂΩïËøáÊúüÔºåÈúÄË¶ÅÈáçÊñ∞ÁôªÂΩïÔºåÊ∏ÖÁ©∫vuex‰∏≠ÁöÑtoken Âíå localstorage‰∏≠ÁöÑtoken
        store.commit('settoken','')
        localStorage.removeItem('token')
        //Ë∑≥ËΩ¨Âà∞loginÈ°µÈù¢ÈáçÊñ∞ÁôªÂΩï
        router.replace({path:'/login'})
      }
      return data
    }
    return response
  })
}


