import Vue from 'vue'
//ÂºïÂÖ•cube-uiÁªÑ‰ª∂Â∫ì
import './cube-ui'
import App from './App.vue'
//‰∏çÂÜô/index.jsÔºåË∑ØÂæÑ‰ºöÈªòËÆ§ÂåπÈÖçindex.js
import router from './router'
import store from './store'
import axios from 'axios'
import 'amfe-flexible'
import setaxios from './setaxios'
//ÊåâÈúÄÂºïÂÖ•Vant‰∏≠ÁöÑSearchÁªÑ‰ª∂
import { Search } from 'vant'

//ÈÄöËøáVue.useÊ≥®ÂÜåVant‰∏≠ÁöÑSearchÁªÑ‰ª∂
Vue.use(Search)

//Âú®ÂÖ®Â±ÄÁéØÂ¢É‰∏ãÔºåÊâßË°åsetaxiosÊñπÊ≥ï
setaxios()

//ÂÖ®Â±ÄË∑ØÁî±ÂÆàÂç´
router.beforeEach((to,from,next) => {
  //Êó†ËÆ∫ÊòØÂà∑Êñ∞ËøòÊòØË∑≥ËΩ¨Ë∑ØÁî±ÔºåÁ¨¨‰∏Ä‰∏™ËøõÂÖ•ÁöÑÂ∞±ÊòØÂÖ®Â±ÄÂâçÁΩÆË∑ØÁî±ÂÆàÂç´Èí©Â≠êÂáΩÊï∞
  //Áî®Êà∑ÁôªÂΩïÊàêÂäü‰ª•ÂêéÔºåÂ∞Ülogin.vue‰∏≠ÈÄöËøáwindow.localStorage.setItem‰øùÂ≠òÂú®Êú¨Âú∞ÁöÑtokenÂÄºÂÖàÈÄöËøálocalstorage.getItem()ÂèñÂá∫,ÂÜçÈÄöËøávuexÁöÑcommitË∑ümutationsÂØπËØùÔºå‰ªéËÄåÂ∞Ütoken‰øùÂ≠òËá≥vuexÁöÑstateÊï∞ÊçÆ‰ªì‰∏≠
  store.commit('settoken',localStorage.getItem('token'))
  //Â¶ÇÊûúË∑ØÁî±ÊùÉÈôê‰∏∫tureÔºàtoÂ∞±ÊòØË∑ØÁî±ÈÖçÁΩÆÂØπË±°{}Ôºâ
  if(to.meta.requireAuth){
    //Â¶ÇÊûúVuexÁöÑstore‰∏ãÁöÑstateÊï∞ÊçÆ‰ªì‰∏≠ÁöÑtokenÊúâÈùûÁ©∫ÂÄº
    if(store.state.token){
      //ÊîæË°åÔºå‰∫à‰ª•Ë∑≥ËΩ¨
      next()  
    }else{
      //Â¶ÇÊûústateÊï∞ÊçÆ‰ªì‰∏≠ÁöÑtoken‰∏∫Á©∫ÂÄº
      next({
        //Ë∑≥ËΩ¨Ëá≥ÁôªÂΩïÈ°µÈù¢ÈáçÊñ∞ÁôªÂΩï
        path:'/login',
        //ÊçéÂ∏¶‰∏Ä‰∫õÂèÇÊï∞ÔºåÈáçÂÆöÂêëËá≥fullPath(ÂÆåÊï¥ÁöÑË∑ØÂæÑ)ÔºåÂç≥Á®çÂêéÁî®Êà∑ÁôªÂΩïÊàêÂäü‰ª•ÂêéÂ∞ÜÁ´ãÈ©¨Ë∑≥ËΩ¨Ëá≥ÂàöÊâçÊú™ÁôªÂΩïÊó∂ÊâÄÁÇπÂáªÁöÑ‰ΩÜÊó†Ê≥ïËÆøÈóÆÁöÑÈ°µÈù¢ÁöÑ‚ÄúÂÆåÊï¥Ë∑ØÂæÑ‚Äù
        query:{redirect:to.fullPath}  
      })
    }
  }else{
    next()
  }
})

Vue.config.productionTip = false
//Â∞ÜaxiosÊîæÂú®VueÂéüÂûã‰∏äÔºåÂèØÂÖ®Â±Ä‰ΩøÁî®(ÂåÖÊã¨vc)
Vue.prototype.$http = axios

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

