                min:'Áî®Êà∑Âêç‰∏çËÉΩÂ∞ë‰∫é3‰∏™Â≠óÁ¨¶',
                max:'Áî®Êà∑Âêç‰∏çËÉΩÂ§ß‰∫é15‰∏™Â≠óÁ¨¶'
