        //vcÂà©Áî®ÊîæÂú®VueÂéüÂûã‰∏äÁöÑ$http(‰πüÂ∞±ÊòØaxios)Âêë‚ÄúÊ≥®ÂÜåÊé•Âè£‚ÄùÂèëÈÄÅgetËØ∑Ê±Ç
        // `params` ÊòØ‰∏éËØ∑Ê±Ç‰∏ÄËµ∑ÂèëÈÄÅÁöÑ URL ÂèÇÊï∞
