<template>
  <div>
    <img class="headerimg" src="../assets/Ê∏ÖÊñ∞Â≠¶Èô¢È£é.png" alt="">
    <!-- 
      ‚ö°ÂºïÂÖ•cube-formÁªÑ‰ª∂ÂêéÔºåRegister(Áà∂ÁªÑ‰ª∂)Áªôcube-form(Â≠êÁªÑ‰ª∂)Âä®ÊÄÅ‰º†ÈÄí ‚ÄúÂØπË±°Á±ªÂûãÁöÑprops‚Äù / "ÁªëÂÆö‰∏Ä‰∏™Ëá™ÂÆö‰πâ‰∫ã‰ª∂"Ôºå‰ªéËÄåÂÆûÁé∞Â≠êÁªôÁà∂‰º†ÈÄíÊï∞ÊçÆ
      Â≠êÁªÑ‰ª∂ÁöÑpropsÊé•Êî∂Âà∞ :model„ÄÅ:schema„ÄÅ@submit Ëøô‰∏âÁªÑÊï∞ÊçÆÂêéÔºåÂ∞Ü‰Ωøcube-formÁªÑ‰ª∂Ëß£ÊûêÂÖ∂Ê®°ÊùøÔºå‰ªéËÄåÂ∞Ücube-formÁªÑ‰ª∂ÁöÑÁªìÊûÑÊ∏≤ÊüìËá≥È°µÈù¢
    -->
    <!-- üìå‰ΩøÁî®Vue‰∏≠ÁöÑ‰∫ã‰ª∂‰øÆÈ•∞Á¨¶-preventÈòªÊ≠¢ÈªòËÆ§‰∫ã‰ª∂(ÂèØÂ∫îÁî®‰∫éËá™ÂÆö‰πâ‰∫ã‰ª∂) -->
    <cube-form 
      :model="model"  
      :schema="schema"
      @submit.prevent="submitHandler" 
    />
  </div>
</template>

<script>
  export default {
    name:'Login',
    //Êï∞ÊçÆ
    data() {
      return {
        //‚ö°modelÂ∞±ÊòØÊï¥‰∏™Ë°®ÂçïÈúÄË¶ÅÁöÑÊï∞ÊçÆÊ∫ê
        //model-ÈÖçÁΩÆÁî®Êà∑ÂêçÂíåÂØÜÁ†Å
        model:{
          username:'',
          password:''
        },
        //‚ö°schemaÂ∞±ÊòØÁîüÊàêË°®ÂçïÊâÄÂÆö‰πâÁöÑÊ®°ÂºèÔºàschema-Ê®°ÂºèÔºâ
        schema:{
          fields:[
            //üìåÁî®Êà∑ÂêçÈÖçÁΩÆ
            {
              type:'input',
              modelKey:'username',
              label:'Áî®Êà∑Âêç',
              //ËæìÂÖ•Ê°ÜÊâÄÊèêÁ§∫ÁöÑÂÜÖÂÆπ
              props:{
                placeholder:'ËØ∑ËæìÂÖ•Áî®Êà∑Âêç'
              },
              rules:{
                //Ê†°È™åËßÑÂàô
                required:true, //ÂøÖÂ°´ -ÈÖçÁΩÆËØ•È°πÂêéÈ°µÈù¢‰ºöÂ±ïÁé∞ * Ê†áÂøó
                type:'string',
                min:3,
                max:15
              },
              //Â§±ÂéªÁÑ¶ÁÇπÔºåÂ∞±‰ºöËß¶ÂèëüëámessagesÂÜÖÁöÑÊ†°È™åÈ°π
              trigger:'blur',
              messages:{
                required:'Áî®Êà∑Âêç‰∏çËÉΩ‰∏∫Á©∫',
                min:'Áî®Êà∑Âêç‰∏çËÉΩÂ∞ë‰∫é3‰∏™Â≠óÁ¨¶',
                max:'Áî®Êà∑Âêç‰∏çËÉΩÂ§ß‰∫é15‰∏™Â≠óÁ¨¶'
              }
            },
            //üìåÂØÜÁ†ÅÈÖçÁΩÆ
            {
              type:'input',
              modelKey:'password',
              label:'ÂØÜÁ†Å',
              props:{
                placeholder:'ËØ∑ËæìÂÖ•ÂØÜÁ†Å',
                type:'password',
                eye:{
                  open:false,
                }
              },
              rules:{
                //Ê†°È™åËßÑÂàô
                required:true, //ÂøÖÂ°´ -ÈÖçÁΩÆËØ•È°πÂêéÈ°µÈù¢‰ºöÂ±ïÁé∞ * Ê†áÂøó
              },
              //Â§±ÂéªÁÑ¶ÁÇπÔºåÂ∞±‰ºöËß¶ÂèëÊ†°È™å
              trigger:'blur'
            },
            //Êèê‰∫§ÊåâÈíÆ
            {
              type:'submit',
              label:'ÁôªÂΩï'
            }
          ]
        }
      }
    },
    methods: {
      //ÁºñÂÜôÂºÇÊ≠•Ë∞ÉÁî®
      async submitHandler(e){
        // e.preventDefault()  //üìåÁî±‰∫éË°®ÂçïÊèê‰∫§‰ª•Âêé‰ºö‰∫ßÁîü‚ÄúËá™Âä®Âà∑Êñ∞È°µÈù¢‚ÄùÁöÑÈªòËÆ§Ë°å‰∏∫ÔºåÊâÄ‰ª•ÂèØÁî®eventÁöÑpreventDefault()ÊñπÊ≥ïÊù•‚ÄúÈòªÊ≠¢ÂÖÉÁ¥†ÂèëÁîüÈªòËÆ§Ë°å‰∏∫‚Äù‚ùó‚ùó
        //C:\Users\GUI WEI\Desktop\Vue2+3_desktop\AJAX\Code\5-axios-AJAXÊúâ‰ª£Á†ÅËß£ÊûêËøáÁ®ã
        //vcÂà©Áî®ÊîæÂú®VueÂéüÂûã‰∏äÁöÑ$http(‰πüÂ∞±ÊòØaxios)Âêë‚ÄúÁôªÂΩïÊé•Âè£‚ÄùÂèëÈÄÅgetËØ∑Ê±Ç
        try{
          //üìåaxiosËØ∑Ê±ÇËøîÂõûÁöÑÁªìÊûú‰∏∫resultÔºåresult.data-‰ªéÁôªÂΩïÊé•Âè£ËøîÂõûÁöÑÊï∞ÊçÆÔºåresult.data.xxx-ÊâÄËøîÂõûÊï∞ÊçÆ‰∏≠ÁöÑÊüêÊüêÂ±ûÊÄß(Â¶Çtoken„ÄÅcode„ÄÅmessage)
          const result = await this.$http.get('/api/login',{params:this.model})
          //ËØ∑Ê±ÇÊàêÂäüÔºàÂç≥Áî®Êà∑ÊàêÂäüÁôªÂΩïÊó∂Ôºâ
          if(result.code == '0'){
            //üìåÈÄöËøácommitË∞ÉÁî®mutationsÂêéÔºåÂà©Áî®settokenÊñπÊ≥ïÔºåÊìç‰Ωú‰ªéÁôªÂΩïÊé•Âè£‰º†ËøáÊù•ÁöÑtokenÔºåÂπ∂ÈÄöËøámutationsÂ∞ÜÂÖ∂ÂÄº‰øùÂ≠òÂà∞stateÊï∞ÊçÆ‰ªì‰∏≠
            this.$store.commit('settoken',result.token)
            //üìåÈÄöËøávuexÂ≠òÂÇ®ÔºåÂè™Ë¶ÅÂà∑Êñ∞È°µÈù¢ÔºåÊï∞ÊçÆ‰æø‰ºö‰∏¢Â§±„ÄÇÊâÄ‰ª•ËøòË¶ÅÂ≠òÂÇ®Ëá≥localstorageÊú¨Âú∞Â≠òÂÇ®‰∏≠
            window.localStorage.setItem('token',result.token)
            
            //Áî®Êà∑ÊàêÂäüÁôªÂΩïÂêéÔºåÂà§Êñ≠Ë∑ØÁî±ÊòØÂê¶Â∏¶ÂèÇÔºåÂ¶ÇÊûúÂ∏¶ÂèÇÂ∞±ÂéªÂà∞ÈáçÂÆöÂêëÂèÇÊï∞ÁöÑÂú∞ÂùÄ(‰πüÂ∞±ÊòØÊú™ÁôªÂΩïÊó∂Áî®Êà∑ÊÉ≥ÁÇπÂºÄ‰ΩÜÂèà‰∏çËÉΩÊòæÁ§∫ÁöÑÈ°µÈù¢)ÔºåÂê¶ÂàôÂ∞±ÂéªÈ¶ñÈ°µ
            if(this.$route.query.redirect){
              //Â¶ÇÂ∏¶ÂèÇÊï∞ÔºåË∑≥ËΩ¨Ëá≥this.$route.query.redirectÔºà‰πüÂ∞±ÊòØmain.js‰∏≠query‰∏≠ÁöÑredirectÔºâ
              this.$router.replace({path:this.$route.query.redirect})
            }else{
              //Â¶Ç‰∏çÂ∏¶ÂèÇÊï∞ÔºåÂàôË∑≥ËΩ¨Ëá≥È¶ñÈ°µindex„ÄêÁºñÁ®ãÂºèË∑ØÁî±ÂØºËà™ -> replace: Ë∑≥ËΩ¨ÂêéÊó†Ê≥ïÂõûÈÄÄ‰∏ä‰∏ÄÁ∫ßÈ°µÈù¢ÔºåËã•Â∞ÜreplaceÊç¢ÊàêpushÂàôÂèØ‰ª•ÂõûÈÄÄ‰∏ä‰∏ÄÁ∫ßÈ°µÈù¢„Äë
              this.$router.replace({path:'/botnav/index'})
            }
          }else{   //ËØ∑Ê±ÇÂ§±Ë¥•
            alert(result.message)
          } 
        }catch(err){
          console.log(err)
        }
      }
    },
  }
</script>

<style lang="stylus" scoped>
  .headerimg
    height 200px
    width 100%
