import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let store =  new Vuex.Store({

  //üìåstate-Â≠òÂÇ®ÂèòÈáèÁöÑÊï∞ÊçÆ‰ªìÂ∫ì    Â≠òtoken
  state: {
    token:'',
    //üìåÂ¶ÇÊûúÊú¨Âú∞Â≠òÂÇ®localStorage‰∏≠ÊúâÁªèËøáÁî®Êà∑ÁöÑÊ∑ªÂä†ÊàñÂáèÂ∞ëÊâÄ‰øùÂ≠òÁöÑ‚ÄúÂïÜÂìÅ‰ø°ÊÅØ‚ÄùÔºåÂàôÂà∑Êñ∞È°µÈù¢Âêé‚ÄúËØªÂèñ‚ÄùÊú¨Âú∞Â≠òÂÇ®ÔºåÂê¶ÂàôÔºåË¥≠Áâ©ËΩ¶ÂïÜÂìÅ‰∏∫Á©∫
    cartarry: JSON.parse(localStorage.getItem('cartarry')) || []  //Â≠òÂÇ®Ë¥≠Áâ©ËΩ¶ÂïÜÂìÅÁöÑÊï∞ÁªÑ    
  },

  //üìåmutations-ÈÄöËøá‰∏Ä‰∫õÊñπÊ≥ïÂéªÊìç‰Ωústate‰∏≠ÁöÑÊï∞ÊçÆÔºåÂ±û‰∫é-‚ÄúÂêåÊ≠•Êìç‰Ωú‚Äù
  mutations: {

    //ËÆæÁΩÆvuexÁöÑtoken
    settoken(state,token){   //ËØ•token‰∏∫this.$store.commit‰∏≠‰ªéÁôªÂΩïÊé•Âè£ÂìçÂ∫îËøîÂõûÁöÑresult.data.token(Âç≥Áî®Êà∑ÊàêÂäüÁôªÂΩï‰ª•ÂêéÂä†ÂØÜÁîüÊàêÁöÑtoken‰ª§Áâå)
      state.token = token   //Â∞ÜÂæÖ‰ºö‰º†ÂÖ•ÁöÑtokenÁöÑÂÄº‰øùÂ≠òÂà∞stateÊï∞ÊçÆ‰ªì‰∏≠
    },

    //Ê∑ªÂä†ÂïÜÂìÅÂà∞Ë¥≠Áâ©ËΩ¶
    tocart(state,tag){
      //üìåÂà§Êñ≠‰ªé‚ÄúÂàÜÁ±ª‚ÄùÈ°µ(List.vue)‰∏≠Ê∑ªÂä†ÁöÑÂïÜÂìÅÁöÑÂêçÁß∞tag.label‰∏é‚ÄúË¥≠Áâ©ËΩ¶‚Äù(Cart.vue)‰∏≠ÁöÑÂïÜÂìÅÁöÑÂêçÁß∞ÊòØÂê¶Áõ∏Á≠âÔºàcartarry‰∏ÄÂºÄÂßãËÇØÂÆöÊòØÁ©∫Êï∞ÁªÑÔºåÊâÄ‰ª•‰∏ÄÂºÄÂßãËÇØÂÆöÊâßË°åelseËØ≠Âè•Âêëcartarry‰∏≠push‰∫ÜÊüê‰∏™ÂïÜÂìÅÔºåÈÇ£‰πàÊ≠§Êó∂cartarryÂ∞±‰∏çÊòØÁ©∫Êï∞ÁªÑ‰∫ÜÔºåËÄåÊòØ[{title:tag.label,cartCount:1}]ÔºâÔºåÂêéÁª≠ÂÜç‚ÄúÊ∑ªÂä†ÂïÜÂìÅÂà∞Ë¥≠Áâ©ËΩ¶‚ÄùÔºåÂ∞±ÂèØ‰ª•ÊòéÊòæÂú∞Âà§Êñ≠ element.title == tag.labelÊòØÂê¶ÊàêÁ´ã‰∫Ü
      //Â∞Üarray.find(callback())ËøîÂõûÁöÑÊï∞ÁªÑ‰∏≠Êª°Ë∂≥ÂõûË∞ÉÁöÑÁ¨¨‰∏Ä‰∏™ÂÖÉÁ¥†ÁöÑÂÄºËµãÂÄº‰∫égoods
      let goods = state.cartarry.find(element => element.title == tag.label) 
      if(goods){
        //Â¶ÇÊûúÂ≠òÂú®ÂêçÁß∞Áõ∏Á≠âÁöÑÈÇ£È°πÂïÜÂìÅÔºåÂàôËÆ©ËØ•ÂïÜÂìÅÊï∞Èáè+1
        goods.cartCount += 1
      }else{
        //Âê¶ÂàôÔºåÂêëcartarryÊï∞ÁªÑ‰∏≠Ê∑ªÂä†Áî®Êà∑ÊâÄÁÇπÂáªÁöÑÈÇ£‰∏™ÂïÜÂìÅ
        state.cartarry.push({title:tag.label,cartCount:1,pic:tag.image})
      }
    },

    //Ë¥≠Áâ©ËΩ¶ÂïÜÂìÅÊï∞ÈáèÂä†‰∏Ä
    cartadd(state,index){  //index-Ê†áËØÜÊâßË°åÂä†‰∏ÄÊìç‰ΩúÁöÑÂïÜÂìÅÁöÑ‚ÄúÁ¥¢Âºï‚Äù
      //Áî®Êà∑ÊØèÁÇπÂáª‰∏ÄÊ¨°Êüê‰∏™ÂïÜÂìÅÁöÑ‚Äú+ÊåâÈíÆ‚ÄùÔºåË¥≠Áâ©ËΩ¶‰∏≠ÁöÑÈÇ£‰∏™ÂïÜÂìÅÁöÑÊï∞ÁõÆÂ∞±‚ÄúÂä†1‚Äù
      state.cartarry[index].cartCount++
    },

    //Ë¥≠Áâ©ËΩ¶ÂïÜÂìÅÊï∞ÈáèÂáè‰∏Ä
    cartremove(state,index){
      //Áî®Êà∑ÊØèÁÇπÂáª‰∏ÄÊ¨°Êüê‰∏™ÂïÜÂìÅÁöÑ‚Äú-ÊåâÈíÆ‚ÄùÔºåË¥≠Áâ©ËΩ¶‰∏≠ÁöÑÈÇ£‰∏™ÂïÜÂìÅÁöÑÊï∞ÁõÆÂ∞±‚ÄúÂáè1‚Äù
      if(state.cartarry[index].cartCount > 1){
        //Â¶ÇÊûúËØ•ÂïÜÂìÅÊï∞ÁõÆÂ§ß‰∫é1ÔºåÂàôÊï∞ÈáèÂèØ‰ª•Âáè‰∏Ä
        state.cartarry[index].cartCount--
      }else{
        //üìåÂê¶ÂàôwindowÂºπÁ™óËØ¢ÈóÆÊòØÂê¶ÁßªÈô§ËØ•ÂïÜÂìÅÔºüÂ¶ÇÈúÄË¶ÅÁßªÈô§ÔºåÂàôË∞ÉÁî®arrayÁöÑspliceÊñπÊ≥ï‰ªéÊï∞ÁªÑ‰∏≠ÁöÑÊåáÂÆö‰ΩçÁΩÆÁßªÈô§ÂØπÂ∫îÁöÑÂïÜÂìÅ
        if(window.confirm('Á°ÆÂÆö‰ªéË¥≠Áâ©ËΩ¶ÁßªÈô§ÂïÜÂìÅÂêóÔºü')){
          state.cartarry.splice(index, 1)
        }
      }
    },

    //Ê∏ÖÁ©∫Ë¥≠Áâ©ËΩ¶
    clearcart(state){
      state.cartarry = []
    }

  },

  //üìåÊèê‰∫§mutationÔºåÂºÇÊ≠•Êìç‰Ωú
  actions: {

  },

  //üìåÁ±ª‰ºº‰∫écomputedËÆ°ÁÆóÂ±ûÊÄßÁöÑÁÆÄÂÜôÂΩ¢ÂºèÔºåÂ∞ÜÊñπÊ≥ï‰∏≠ÁöÑÁªìÊûú‰ª•ËøîÂõûÂÄºÁöÑÂΩ¢Âºè‰º†ÈÄí
  getters:{
    //ÂÆö‰πâcountsumÊñπÊ≥ïÁî®‰∫éÂ∞ÜË¥≠Áâ©ËΩ¶ÂïÜÂìÅÁöÑÊù°Êï∞ÈöèÁùÄÁî®Êà∑ÁÇπÂáªÂ¢ûÂä†ÊåâÈíÆ‰πãÂêéÔºå‰πü‰ºöÈöè‰πãÂä†1
    countsum:state => {
      let num = 0
      //ÂØπ‰∫écartarry‰∏≠ÁöÑÂÖÉÁ¥†{title:tag.label,cartCount:1}ÔºåÊØèÈÅçÂéÜ‰∏Ä‰∏™ÔºånumÂ∞±Âä†1Ôºå‰ΩøÂæó(Ë¥≠Áâ©ËΩ¶Cart.vue)ÂæÖ‰ºöËé∑ÂèñÂà∞ÁöÑÂÄºÊòØ
      state.cartarry.forEach(element => num += element.cartCount)
      //üìåcountsumÁöÑÂÄºÂ∞±ÊòØËøô‰∏™numÔºåBotnav.vue‰∏≠‰ºöÈÄöËøá...mapGetters(['countsum'])‰ªévuex‰∏≠ÊãøÂà∞ËØ•ÂÄºÊîæÂú®ËØ•ÁªÑ‰ª∂ÁöÑÊ®°ÊùøÁöÑ<span>Ê†áÁ≠æ‰∏≠Âä®ÊÄÅÂú∞Ê∏≤ÊüìËá≥È°µÈù¢
      return num
    }
  }

})

//ÁõëÂê¨Âú®ÊØèÊ¨°Ë∞ÉÁî®mutationsÊó∂ÔºåÈÉΩ‰ºöÂÖàËøõÂÖ•‚ÄústoreÁöÑsubscribeËøô‰∏™ÊñπÊ≥ï‚ÄùÔºåÁÑ∂ÂêéÊàë‰ª¨ÂèØ‰ª•Âú®Ëøô‰∏™ÊñπÊ≥ïÂÜÖÊâßË°å‰∏Ä‰∫õËá™Â∑±ÊÉ≥Ë¶ÅÁöÑÂ§ÑÁêÜ
store.subscribe((mutations,state) => {
  //ÊääÊï∞ÊçÆÂ≠òÂÇ®Âà∞Êú¨Âú∞localStorage‰∏≠
  localStorage.setItem('cartarry',JSON.stringify(state.cartarry))
})

export default store
